<template>
    <div class="contact-background">
        <h1 class="contact-cantact">联络我们</h1>
        <img class="contact-line" src="../../assets/green-line.png" alt="green-line">
        <p class="contact-text">对于迈入工业4.0转型仍需要更多的帮助吗？<br>或是想了解更多的解决方案<br>欢迎点击以下连结获取更多协助！</p>
        <div class="contact-button-box">
            <button v-ga="$ga.commands.trackEvent.bind(this, gaConsult[0].category, gaConsult[0].label)" @click="showConsult" class="contact-button-primary">查询行业顾问</button>
            <button v-ga="$ga.commands.trackEvent.bind(this, gaConsult[1].category, gaConsult[1].label)" @click="seekSolution" class="contact-button-gray">更多解决方案</button>
        </div>
        <contact-us-dialog
          v-if='showConsultPopup'
          @closePage="closeConsult"
        ></contact-us-dialog>
    </div>
</template>

<script>
import ContactUsDialog from '@/components/dialog/ContactUsDialog'

export default {
  name: 'ContactUs',
  components: {
    ContactUsDialog
  },
  props: {
    gaConsult: { type: Array }
  },
  data () {
    return {
      showConsultPopup: false
    }
  },
  methods: {
    showConsult () {
      this.showConsultPopup = true
    },
    closeConsult () {
      this.showConsultPopup = false
    },
    seekSolution () {
      window.open('http://www.synergies.com.tw/sygps.html')
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .contact-background{
      padding-top: 32px;
      padding-bottom: 80px;
      background-color: $theme-color-thin-black;

        .contact-cantact{
            text-align: center;
            color: $theme-color-primary;
        }

        .contact-text{
            text-align: center;
        }

        .contact-line{
            max-width: 320px;
            display:block;
            margin:auto;
        }

        .contact-button-box{
            height: 150px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            .contact-button-gray{
                width: 320px;
                height: 52px;
                border: 1px solid #FFFFFF;
                background: transparent;
                color: $theme-color-white;
                cursor: pointer;
            }

            .contact-button-primary{
                width: 320px;
                height: 52px;
                background: linear-gradient(90deg, #0BB5F7 0%, #06DBC3 100%);
                color: #FFFFFF;
                cursor: pointer;
            }
        }
    }

    @media( min-width: 1023px ){
      .contact-background{

        .contact-button-box{
          width: 768px;
          display: flex;
          flex-direction: row;
          justify-content: space-evenly;
          margin: auto;
        }
      }
    }
</style>



