<template>
    <div class="contact-us-dialog">
      <button type="button" @click.prevent='closeDialog' class="dialog-close-btn">X</button>
      <div class="dialog-up-web">
        <h2 class="dialog-head">咨询资料</h2>
        <p class="dialog-head-text">请填写以下资料，确认后我们将会有咨询专员与您联络</p>
      </div>
      <div class="dialog-box-web">
        <div class="dialog-left-box-web">
          <p>基本资料</p>
          <div class="inline-input-block">
            <div class="input-block">
              <input type="text"
                name="contactName"
                placeholder="名称"
                v-validate="'required'"
                v-model="contactData.CONTACT_NAME"
              >
              <div class="error-msg"
                v-show="errors.has('contactName')"
              >{{ errors.first('contactName') }}</div>
            </div>
            <div class="input-block">
              <input type="text"
                name="contactTitle"
                placeholder="职称"
                v-validate="'required'"
                v-model="contactData.CONTACT_TITLE"
              >
              <div class="error-msg"
                v-show="errors.has('contactTitle')"
              >{{ errors.first('contactTitle') }}</div>
            </div>
          </div>
          <div class="input-block">
            <input type="text"
              name="mobilePhone"
              v-validate="'required'"
              v-model="contactData.MOBILE_PHONE"
              placeholder="连络电话"
              class="input-half-web"
            >
            <div class="error-msg"
              v-show="errors.has('mobilePhone')"
            >{{ errors.first('mobilePhone') }}</div>
          </div>
          <p>公司资料</p>
          <div class="input-block">
            <input type="text"
              name="companyName"
              placeholder="公司名称"
              v-validate="'required'"
              v-model="contactData.COMPANY_NAME"
            >
            <div class="error-msg"
              v-show="errors.has('companyName')"
            >{{ errors.first('companyName') }}</div>
          </div>
          <div class="input-block">
            <input type="text"
              name="companyPhone"
              placeholder="公司连络电话"
              v-validate="'required'"
              v-model="contactData.COMPANY_PHONE"
            >
            <div class="error-msg"
              v-show="errors.has('companyPhone')"
            >{{ errors.first('companyPhone') }}</div>
          </div>
          <div class="input-block">
            <select class="dialog-select"
              name="cityCode"
              v-model="contactData.CITY_CODE"
              v-validate="'required'"
              @change='filterCategory'
            >
              <option value="" disabled selected hidden>企业所在地</option>
              <option
                v-for="(option, index) in questionList[0].optionList"
                :key="index"
                :value="option.code"
              >{{option.text}}</option>
            </select>
            <div class="error-msg"
              v-show="errors.has('cityCode')"
            >{{ errors.first('cityCode') }}</div>
          </div>
          <div class="input-block">
            <select class="dialog-select"
              name="categoryCode"
              v-model="contactData.CATEGORY_CODE"
              v-validate="'required'"
            >
              <option value="" disabled selected hidden>产业类别</option>
              <option
                v-for="(option, index) in categoryList"
                :key="index"
                :value="option.code"
              >{{option.text}}</option>
            </select>
            <div class="error-msg"
              v-show="errors.has('categoryCode')"
            >{{ errors.first('categoryCode') }}</div>
          </div>
          <p>认证信箱</p>
          <div class="input-block">
            <input type="email"
              name="companyEmail"
              placeholder="请输入公司信箱"
              v-validate="'required'"
              v-model="contactData.COMPANY_EMAIL"
            >
            <div class="error-msg"
              v-show="errors.has('categoryCode')"
            >{{ errors.first('categoryCode') }}</div>
          </div>
        </div>
        <div>
          <p>咨询内容</p>
          <div class="input-block">
            <select class="dialog-select"
              name="contactTime"
              v-model="contactData.CONTACT_TIME"
              v-validate="'required'"
            >
              <option value="" disabled selected hidden>方便连络时段</option>
              <option>早上 09：00-12：00</option>
              <option>上午 12：00-15：00</option>
              <option>下午 15：00-18：00</option>
            </select>
            <div class="error-msg"
              v-show="errors.has('contactTime')"
            >{{ errors.first('contactTime') }}</div>
          </div>
          <div class="input-block">
            <datepicker class="dialog-select-calendar"
              name="importSystemTime"
              v-validate="'required'"
              v-model="contactData.IMPORT_SYSTEM_TIME"
              format="MM/yyyy"
              minimum-view="month"
              :language="zh"
              placeholder= "预计导入时间"
            ></datepicker>
            <div class="error-msg"
              v-show="errors.has('importSystemTime')"
            >{{ errors.first('importSystemTime') }}</div>
          </div>
          <div class="input-block">
            <select class="dialog-select"
              name="budget"
              v-validate="'required'"
              v-model="contactData.BUDGET"
            >
              <option value="" disabled selected hidden>预计金额</option>
              <option>300 万以下</option>
              <option>300-500 万</option>
              <option>500-1000 万</option>
              <option>1000 万以上</option>
            </select>
            <div class="error-msg"
              v-show="errors.has('budget')"
            >{{ errors.first('budget') }}</div>
          </div>
          <div class="input-block">
            <textarea class="dialog-textarea"
              name="remark"
              cols="50"
              rows="30"
              placeholder="请提供您想谘询的问题，或是想导入的系统"
              v-validate="'required'"
              v-model="contactData.REMARK"
            ></textarea>
            <div class="error-msg"
              v-show="errors.has('remark')"
            >{{ errors.first('remark') }}</div>
          </div>
          <button v-ga="$ga.commands.trackEvent.bind(this, '/asksch', 'S-Asend_click')" @click="sendDialogForm" class="submit-btn" :class="{formActive: sendForm}">送出</button>
        </div>
      </div>
      <popupBox v-on:closePopupBox="closePopupBox" v-if="showPopupBox" :popupPass="popupPass"></popupBox>
      <div class="dialog-line-web"></div>
    </div>
</template>

<script>

import popupBox from './PopupBox'
import Datepicker from 'vuejs-datepicker'
import {zh} from 'vuejs-datepicker/dist/locale'
import { sendCompanyData } from '@/API/analyze'

export default {
  inject: ['$validator'],
  name: 'ContactUsDialog',
  components: {
    Datepicker,
    popupBox
  },
  data () {
    return {
      contactData: {
        QUEST_REPORT: '',
        COMPANY_NO: '',
        CONTACT_NAME: '',
        CONTACT_TITLE: '',
        MOBILE_PHONE: '',
        COMPANY_NAME: '',
        COMPANY_PHONE: '',
        COMPANY_EMAIL: '',
        EMAIL_CHECK: '',
        CITY_CODE: '',
        CATEGORY_CODE: '',
        CONTACT_TIME: '',
        IMPORT_SYSTEM_TIME: '',
        BUDGET: '',
        REMARK: ''
      },
      zh: zh,
      categoryList: [],
      sendForm: false,
      showPopupBox: false,
      popupPass: true
    }
  },
  mounted () {
    for (let i in this.clientData) {
      if (this.clientData[i]) {
        this.contactData[i] = this.clientData[i]
      }
    }
    let dt = new Date().getTime() + ''
    this.contactData.COMPANY_NO = dt.substr(3)
  },
  watch: {
    contactData: {
      deep: true,
      handler (newValue, oldValue) {
        let tempTime = JSON.parse(JSON.stringify(this.contactData.IMPORT_SYSTEM_TIME))
        this.contactData.IMPORT_SYSTEM_TIME = tempTime.substr(0, 7)
        for (let i in this.contactData) {
          if (this.contactData[i] || i === 'EMAIL_CHECK' || i === 'QUEST_REPORT') {

          } else {
            this.sendForm = false
            return
          }
          this.sendForm = true
        }
      }
    }
  },
  computed: {
    questionList () {
      return this.$store.state.questionList
    },
    clientData () {
      return this.$store.state.clientData
    }
  },
  methods: {
    closeDialog () {
      this.$emit('closePage')
    },
    sendDialogForm () {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.$store.commit('setClientData', this.contactData)
          this.showPopupBox = true
          // sendCompanyData(
          //   this.contactData
          // )
          //   .then(response => {
          //     this.showPopupBox = true
          //   })
          //   .catch(error => {
          //     this.showPopupBox = true
          //     this.popupPass = false
          //     console.log(error)
          //   })
        }
      })
    },
    filterCategory (e) {
      let checkText
      if (e.target.options.selectedIndex === 32) {
        checkText = 'TAIWAN'
      } else {
        checkText = 'CHINA'
      }
      this.categoryList = this.questionList[1].optionList.filter(element =>
        element.remark === checkText
      )
    },
    closePopupBox () {
      this.showPopupBox = false
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .contact-us-dialog {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 15px;
    background: #1D2331;
    z-index: 1000;
    overflow-y: auto;

    .dialog-close-btn {
      background-color: transparent;
      color: $theme-color-white;
      border: none;
      display: block;
      text-align: center;
      cursor: pointer;
      margin: 0% 0 0 97%;
    }

    .dialog-head-text {
      color: #BEC6DB;
      margin-bottom: 45px;
    }

    .dialog-select {
      border-radius: 0px;
      width: 100%;
    }

    .dialog-select:focus {
      border-bottom: 1px solid rgba(255, 255, 255, 0.54);
  }

    .dialog-select-calendar{
      border-radius: 0px;
      width: 100%;
      color: $theme-color-black;
    }

    .dialog-textarea {
      display: block;
      width: 100%;
      height: 200px;
      color: $theme-color-white;
      padding: 10px;
    }
    .submit-btn {
      width: 100%;
      height: 45px;
      background-color: $theme-color-light-gray;
      color: $theme-backbround-color-gray;
      margin-bottom: 24px;
      border: none;
      opacity: 0.47;
      cursor: text;
    }

    .formActive{
      background: linear-gradient(90deg, #0BB5F7 0%, #06DBC3 100%);
      color: $theme-color-white;
      cursor: pointer;
      opacity: 1;
    }
  }

  @media( min-width: 767px ){

    .dialog-box-web{
      width: auto;
      margin: auto;
      display: flex;
      justify-content: space-around;
    }

    .dialog-left-box-web{
      width: 48%;
      padding-right: 2%;
    }

    .input-half-web{
      width: 47.5%
    }
  }

  @media( min-width: 1023px ){
    .dialog-up-web{
      width: 980px;
      margin: auto;
    }

    .dialog-box-web{
      width: 1023px;
    }
  }

</style>



