<template>
  <div>
    <div class="home">
        <div class="home-1024-web">
          <div class="home-space">
            <h1 class="home-title home-center">转型</h1>
            <h1 class="home-center">企業现状分析报告</h1>
            <img class="home-line" src="../../assets/green-line.png" alt="green-line">
            <p class="home-title-text home-center">在工业4.0的转型过程，首要分析企业现状，針對企业弱项安排转型策略，才能逐步实践工业4.0带给企业的好处。</p>
            <p class="home-center home-f">目前受测企业共</p>
            <div class="home-company-total">
              <p class="home-company-total-text home-center">{{companyNum}} </p>
            </div>
            <div class="div-top-web">
              <p>受测企业 TOP3</p>
              <div class="div-top">
                <p class="div-top-industry">{{topIndustry.topOne}}</p>
                <p class="div-top-industry">{{topIndustry.topTwo}}</p>
                <p class="div-top-industry">{{topIndustry.topThree}}</p>
              </div>
            </div>
          </div>
          <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-go_click')" @click="analyzeQuestion" class="home-analyze" :class="{isFixed: analyzeBarFixed}">立即分析获得<img class="home-analyze-arrow" src="../../assets/white-arrow-right.png" alt="white-arrow"></div>
        </div>

        <div class="div-none-web">
          <h2 class="home-center">受测企业 TOP3</h2>
          <img class="home-white-line home-line" src="../../assets/white-line.png" alt="white-line">
          <div class="home-top-outside">
            <div class="home-top-inside">
              <div class="home-top-num">1</div>
              <p class="home-top-text">{{topIndustry.topOne}}</p>
            </div>
            <div class="home-top-inside">
              <img class="home-top-blue-light" src="../../assets/blue-light.png" alt="blue-light">
              <div class="home-top-num">2</div>
              <p class="home-top-text">{{topIndustry.topTwo}}</p>
            </div>
            <div class="home-top-inside">
              <div class="home-top-num">3</div>
              <p class="home-top-text">{{topIndustry.topThree}}</p>
            </div>
          </div>
        </div>

        <div class="home-768-web">
          <h1 class="home-dimension home-center">分析</h1>
          <h1 class="home-center">准确判断企业现况</h1>
          <img class="home-line" src="../../assets/green-line.png" alt="green-line">
          <p class="home-center">分析数十家领先企业与研究单位，归纳出八个评估『工业4.0』完成度的参考指标，提供企业详细了解自身现况，以及迈入『工业4.0』的下一步作法！</p>
          <div class="home-1024-bottom-web">
            <div class="home-bottome-web">
              <h3 class="home-color">{{currentDimension.name}}</h3>
              <div class="home-onboard-text home-center">{{currentDimension.description}}</div>
            </div>
            <div class="home-rotate">
              <img class="home-rotate-light" src="../../assets/light.png" alt="light">
              <div class="home-rotate-center">
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-production_click')" @click="changeDimensionText(dimensionText.process)" class="home-rotate-text home-rotate-margin-center" :class="{ active: currentDimension.name == dimensionText.process.name}">生产<br>流程</div>
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-time_click')" @click="changeDimensionText(dimensionText.lounchTime)" class="home-rotate-text" :class="{ active: currentDimension.name == dimensionText.lounchTime.name}">及时<br>上市</div>
              </div>
              <div class="home-rotate-between">
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-esset_click')" @click="changeDimensionText(dimensionText.industryEquipment)" class="home-rotate-text home-rotate-margin-between" :class="{ active: currentDimension.name == dimensionText.industryEquipment.name}">工业<br>设备</div>
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-sudemand_click')" @click="changeDimensionText(dimensionText.suplySync)" class="home-rotate-text" :class="{ active: currentDimension.name == dimensionText.suplySync.name}">供需<br>同步</div>
              </div>
              <div class="home-rotate-between">
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-energy_click')" @click="changeDimensionText(dimensionText.energy)" class="home-rotate-text home-rotate-margin-between home-rotate-word-center" :class="{ active: currentDimension.name == dimensionText.energy.name}">能源</div>
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-qm_click')" @click="changeDimensionText(dimensionText.qualityManagement)" class="home-rotate-text" :class="{ active: currentDimension.name == dimensionText.qualityManagement.name}">品质<br>管理</div>
              </div>
              <div class="home-rotate-center">
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-employee_click')" @click="changeDimensionText(dimensionText.employee)" class="home-rotate-text home-rotate-margin-center home-rotate-word-center" :class="{ active: currentDimension.name == dimensionText.employee.name}">员工</div>
                <div v-ga="$ga.commands.trackEvent.bind(this, '/indexsch', 'S-insupply_click')" @click="changeDimensionText(dimensionText.interiorLogistics)" class="home-rotate-text" :class="{ active: currentDimension.name == dimensionText.interiorLogistics.name}">內部<br>物流</div>
              </div>
            </div>
          </div>
        </div>
        <BackToTop v-if="backToTopShow"></BackToTop>
    </div>
    <ContactUs :gaConsult="gaConsult"></ContactUs>
    <Footer></Footer>
    <div class="footer-margin"></div>
  </div>
</template>

<script>
import Footer from '@/components/layout/Footer'
import BackToTop from '@/components/layout/BackToTop'
import ContactUs from '@/components/contactUs/ContactUs'
import { getJsonCompanyNumAndCategoryData } from '@/API/analyze'

export default {
  name: 'Home',
  components: {
    BackToTop,
    ContactUs,
    Footer
  },
  data () {
    return {
      gaConsult: [
        {
          category: '/indexsch',
          label: 'S-consult_click'
        },
        {
          category: '/indexsch',
          label: 'S-solution_click'
        }
      ],
      companyNum: '',
      topIndustry: {
        topOne: '',
        topTwo: '',
        topThree: ''
      },
      analyzeBarFixed: false,
      backToTopShow: false,
      dimensionText: {
        lounchTime: {
          name: '及时上市',
          description: '工业4.0时代，最核心的应用，就是要能利用采集的数据，进行有效的分析优化，进而能缩短产品上市时间。'
        },
        suplySync: {
          name: '供需同步',
          description: '在工业4.0时代，能够串连供应方与需求方的资讯，将有助于优化生产效率的提升。'
        },
        qualityManagement: {
          name: '品质管理',
          description: '如何将大数据妥善应用，将是影响品质的重要关键。'
        },
        interiorLogistics: {
          name: '内部物流',
          description: '生产的过程中，每一个设备/工站到下一个设备/工站之间的物流管理，将是推进智慧化工厂很重要的一部分。'
        },
        employee: {
          name: '员工',
          description: '人员的与时具进以及具备数位化能力，才能够迎接智慧化工厂的时代来临。'
        },
        energy: {
          name: '能源',
          description: '在智慧制造下，已经有越来越多企业重视生产流程与能源消耗之间的关联，并试图优化能耗使用。'
        },
        industryEquipment: {
          name: '工业设备',
          description: '在工业4.0时代，工业设备的重要性，着重在柔性生产以及连网能力的具备。'
        },
        process: {
          name: '生产流程',
          description: '整个生产流程的智能化程度，可从人员与机器的协作、数据的采集与应用到软硬体的相互搭配，是『工业4.0』很重要的一环。'
        }
      },
      currentDimension: {
        name: '及时上市',
        description: '工业4.0时代，最核心的应用，就是要能利用采集的数据，进行有效的分析优化，进而能缩短产品上市时间。'
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
    let response = {
      data: {}
    }
    response.data = {"companyNum":"848","TEXT0":"铁路、船舶、航空航天和其他运输设备制造业","TEXT1":"其他制造业","TEXT2":"计算机、通信和其他电子设备制造业"}
    this.companyNum = response.data.companyNum
    this.topIndustry.topOne = response.data.TEXT0
    this.topIndustry.topTwo = response.data.TEXT1
    this.topIndustry.topThree = response.data.TEXT2
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      let offsetTop = document.querySelector('.home-white-line').offsetTop
      if (scrollTop > offsetTop) {
        this.analyzeBarFixed = true
        this.backToTopShow = true
      } else {
        this.analyzeBarFixed = false
        this.backToTopShow = false
      }
    },
    changeDimensionText (dimension) {
      this.currentDimension.name = dimension.name
      this.currentDimension.description = dimension.description
    },
    analyzeQuestion () {
      this.$router.push({path: '/question'})
    }
  },
  computed: {
    language () {
      return this.$store.state.language
    }
  }
}

</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .home{
    width: 90%;
    margin: auto;

    .home-title{
      text-align: center;
      margin-top: 50px;
    }

    .home-space{
      width: 100%;
      background-image: url('../../assets/space-circle.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position-x: center;
      background-position-y: center;
    }

    .home-title::after{
      content: '工业4.0';
      color: $theme-color-primary;
    }

    .home-title-text{
      margin-bottom: 60px;
    }

    .home-line{
      max-width: 320px;
      display:block;
      margin:auto;
    }

    .home-center{
      text-align: center;
    }

    .home-color{
      color: $theme-color-primary;
    }

    .home-company-total{
      width: 150px;
      height: 44px;
      line-height: 44px;
      background-image: url('../../assets/company-num-box.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position-x: center;
      background-position-y: center;
      font-size: $theme-font-size-x-huge;
      margin: auto auto 60px;

      .home-company-total-text{
        font-weight: 600;
        color: $theme-color-primary;
        background-color: transparent;
      }

      .home-company-total-text::after{
        content: '家';
        color: $theme-color-white;
        font-size: $theme-font-size-large;
      }
    }

    .home-analyze{
      width: 100%;
      height: 52px;
      line-height: 52px;
      font-size: $theme-font-size-large;
      background: linear-gradient(90deg, #0BB5F7 0%, #06DBC3 100%);
      color: $theme-color-white;
      text-align: center;
      margin-bottom: 60px;
      cursor: pointer;

      .home-analyze-arrow{
        width: 15px;
        height: 8px;
        margin-left: 16px;
        animation: arrowSwing 1s ease 0s infinite alternate;
      }
    }

    .home-white-line{
      margin-bottom: 40px;
    }

    .home-top-outside{
      margin-bottom: 40px;
    }

    .home-top-inside{
      position: relative;
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 6px;
    }

    .home-top-blue-light{
      position: absolute;
      left: 10px;
      bottom: 0px;
      width: 45px;
    }

    .home-top-num{
      width: 36px;
      height: 36px;
      background-image: url('../../assets/number-box.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position-x: center;
      background-position-y: center;
      font-size: $theme-font-size-large;
      color: $theme-color-primary;
      text-align: center;
      line-height: 36px;
      margin-right: 12px;
    }

    .home-top-text{
      width: 90%;
      font-size: $theme-font-size-large;
      background: $theme-backbround-color-thin-gray;
      padding: 8px;
      text-align: center;
    }

    .home-dimension:before{
      content: '八大维度';
      color: $theme-color-primary;
    }

    .home-onboard-text{
      padding: 10px;
      margin-bottom: 40px;
      background: $theme-backbround-color-thin-gray;
    }

    .home-rotate{
      position: relative;
      width: 300px;
      height: 300px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      background-image: url('../../assets/dimension-circle.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position-x: center;
      background-position-y: center;
      margin: 40px auto;
    }

    .home-rotate-light{
      position: absolute;
      left: 50%;
      top: 30%;
      width: 50px;

    }

    .home-rotate-center{
      display: flex;
      justify-content: center;
    }

    .home-rotate-between{
      display: flex;
      justify-content: space-around;
    }

    .home-rotate-text{
      font-size: $theme-font-size-large;
      width: 42px;
      background-image: url('../../assets/dimension-option.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position-x: center;
      background-position-y: center;
      cursor: pointer;
      transition: all 0.6s;
      text-align: center;
    }

    .active{
      width: 48px;
      color: $theme-color-primary;
      font-size: $theme-font-size-x-large;
    }

    .home-rotate-margin-center{
      margin-right: 28px;
    }

    .home-rotate-word-center{
      line-height: 40px;
    }

    .home-rotate-margin-between{
      margin-right: 60px;
    }

    .home-rotate-text-center{
      line-height: 36px;
    }

    .isFixed{
      position: fixed;
      bottom: 0;
      left: 0;
      margin-bottom: 0px;
    }

      .div-top-web{
        display: none;
      }
  }
  .footer-margin{
    width: 100%;
    height: 52px;
  }

  @media( min-width: 767px ) and ( max-width: 1023px ){
    .home{

      .home-top-outside{
        display: flex;
        width: 90%;
        margin: auto auto 100px;
      }

      .home-top-num{
        display: none;
      }

      .home-title-text{
        width: 460px;
        margin: auto;
      }

      .home-768-web{
        width: 600px;
        margin: auto;
      }

      .home-bottome-web{
        width: 300px;
        margin: auto;
      }
    }
  }

  @media( min-width: 1023px ){

    .home{

      .home-1024-web{
        width: 1024px;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: space-around;
      }

      .home-space{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }

      .home-title-text{
        text-align: left;
      }

      .home-company-total{
        margin: auto 0px 60px;
        line-height: 34px;
      }

      .home-analyze{
        width: 336px;
      }

      .isFixed{
        position: initial;
        margin-bottom: 60px;
      }

      .div-none-web{
        display: none;
      }

      .home-768-web{
        width: 768px;
        margin: auto;
        padding: 50px 0px;
      }

      .div-top-web{
        display: block;
      }

      .div-top{
        display: flex;
        flex-wrap: wrap;
      }

      .div-top-industry{
        background: rgba(255, 255, 255, 0.24);
        margin: 4px 4px 4px 0px;
        padding: 6px;
        font-size: $theme-font-size-small;
      }

      .home-1024-bottom-web{
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
      }

      .home-bottome-web{
        width: 50%;
      }

    }

    .footer-margin{
      display: none;
    }
  }

  @keyframes arrowSwing{
    0%   {  transform: translateX(0px) }
    100% {  transform: translateX(-8px) }
  }

</style>



