<template>
    <div class="popup-box">
        <button @click="closePopupBox" class="popup-close">x</button>
        <template v-if="popupPass">
            <img class="popup-img" src="../../assets/correct.gif" alt="">
            <h3>感谢您的填写</h3>
            <p class="popup-text">资料已送出，感谢您的填写，之后将会有专人为您服务</p>
        </template>
        <template v-else>
            <img class="popup-img" src="../../assets/error.gif" alt="">
            <h3>上传出了点问题</h3>
            <p class="popup-text">資料在上傳圖中出了點問題，請確保網路連線病病再試一次</p>
        </template>
        <button @click="closePopupBox" class="popup-button">我知道了</button>
    </div>
</template>

<script>

export default {
  name: 'PopupBox',
  props: {
    popupPass: { type: Boolean, default: false }
  },
  methods: {
    closePopupBox () {
      this.$emit('closePopupBox')
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.popup-box{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $theme-backbround-color-thin-gray;
    padding: 30px;
    text-align: center;

    .popup-close{
    width: 95%;
    text-align: right;
    font-size: $theme-font-size-xx-large;
    margin-bottom: 20%;
    cursor: pointer;
    }

    .popup-img{
    width: 120px;
    height: 120px;
    }

    .popup-text{
    color: $theme-color-gray;
    margin-bottom: 30%;
    }

    .popup-button{
    border: 1px solid $theme-color-white;
    width: 90%;
    padding: 10px;
    cursor: pointer;
    }
}
</style>



