<template>
  <img @click="backToTop" class="back-to-top" src="../../assets/arrow-top.png" alt="arrow">
</template>

<script>
export default {
  name: 'BackToTop',
  methods: {
    backToTop: function () {
      let timer = null
      timer = setInterval(() => {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let isSpeed = Math.floor(-osTop / 6)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + isSpeed
        if (osTop === 0) {
          clearInterval(timer)
        }
      }, 30)
    }
  }

}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .back-to-top{
        position: fixed;
        right: 4%;
        bottom: 10%;
        cursor: pointer;
        z-index: 1000;
    }
</style>



